import {httpGet, httpPost} from "../index";
import {isObject} from "../../utils/helpers";


/**
 * 
 * @param params
 * @returns {Promise<*>}
 */
export async function receiveHbao(params = {}) {
    return httpGet('user/get_hongbao', params);
}

