/**
 * () type   1  2  3  4  5 
 * @param params
 * @returns {Promise<*>} {list}
 */
export async function userFuliReport(params = {}) {
    if (params && params.type == 5) {
        return userHongbaoReport(params);
    }
    return userWelfareReport(params);
}

